<template>
  <div>
    <div class="Input">
      <h2>input your text:</h2>
        <textarea placeholder="input" style="width:60%; border:solid 1px #d8d8d8; border-radius:3px; font-size:15px; padding:10px;" rows = "10" @input="descInput" v-model="desc" />
        <span class="numberV">{{txtVal}}</span>
        <p>
          minlength:<input v-model="minlength" style="width:2.5%" placeholder="minlength"@input="number">
          maxlength:<input v-model="maxlength" style="width:2.5%"placeholder="maxlength"@input="number">
        num_beams:<input v-model="num_beams" style="width:2.5%"placeholder="num_beams"@input="number">
        </p>    
      </div>
    <div class="Button">
      <button v-on:click="generate()">click to generate abstract</button>
    </div>
    <div class="Output">
      <h2>output result:</h2>
      <textarea style="width:60%; border:solid 1px #d8d8d8; border-radius:3px; font-size:15px; padding:10px;" rows = "10" v-model="outputText" readonly />
      <span class="numberV">{{resultVal}}</span>
    </div>
  </div>
</template>
<style>

</style>
<script>
import axios from "axios"
  export default{
    name : "first",
      data () {
        return {
            txtVal: 0,
            resultVal: 0,
            desc:"",
            outputText:"",
            maxlength:100,
            minlength:60,
            gold_summary:"",
            num_beams:4
        }
      },
      methods: {
        descInput(){
            this.txtVal = this.desc.split(" ").length
          },
          generate(){
            console.log(this.outputText);
            let data = {
              "content":this.desc,
              "minlength":this.minlength,
              "maxlength":this.maxlength,
              "num_beams":this.num_beams
            };
            axios.post("/snippet/",data).then(res=>{
              console.log(res)
              this.outputText = res.data.abstract;
              this.resultVal = this.outputText.split(" ").length;
            })
          },
          //restrict to number
          number(){
            this.maxlength = this.maxlength.replace(/[^\d]/g,'');
            this.minlength = this.minlength.replace(/[^\d]/g,'');
            this.num_beams = this.num_beams.replace(/[^\d]/g,'');
          }
      }
  }
</script>


